.acf-table > tbody > tr:nth-child(2n) > td.order {
  background: #e9e9e9;
}

.acf-table > tbody > tr:nth-child(2n+1) > td.order {
  background: #ccc;
  color: #fff;
  text-shadow: 0 1px 0 #888;
}

.alternate-colors .acf-table > tbody > tr:nth-child(2n) > td.order {
  background: blue;
  color: #fff;
  text-shadow: none;
}

.alternate-colors .acf-table > tbody > tr:nth-child(2n+1) > td.order {
  background: purple;
  color: #fff;
  text-shadow: none;
}

